import LuigiClient from '@luigi-project/client';
import helpers from '../utils/helpers.js';

class LuigiHelper {
  /**
   * @private
   * @returns {removeInitListener} the function will remove an init listener
   */
  static init() {
    const initListener = LuigiClient.addInitListener(initialContext => {
      const {
        goBackContext
      } = initialContext;
      if (goBackContext?.remotePromiseId) {
        const {
          remotePromiseId,
          action
        } = goBackContext;
        const {
          resolve,
          reject
        } = helpers.unsubscribe(remotePromiseId);
        if (action && action === 'resolve') {
          resolve && resolve({
            msg: 'Luigi Modal Closed',
            ...goBackContext
          });
        } else {
          reject && reject({
            msg: 'Luigi Modal Closed',
            ...goBackContext
          });
        }
      }
      console.log('Initial Context', initialContext);
    });
    return () => {
      LuigiClient.removeInitListener(initListener);
    };
  }
  static getToken() {
    return LuigiClient.getToken();
  }
  static getContext() {
    return LuigiClient.getContext();
  }
  static navigate(path, config) {
    let lm = LuigiClient.linkManager();
    if (config.withoutSync) {
      lm = lm.withoutSync();
    }
    if (config.fromVirtualTreeRoot === true) {
      lm = LuigiClient.linkManager().fromVirtualTreeRoot();
    } else if (config.fromContext === true) {
      lm = LuigiClient.linkManager().fromClosestContext();
    } else if (config.fromContext) {
      lm = LuigiClient.linkManager().fromContext(config.fromContext);
    }
    if (path) {
      console.log('luigi navigate', path);
      lm.navigate(path);
    }
  }
  static updateModalPathInternalNavigation(path, modalSettings, addHistoryEntry = false) {
    LuigiClient.linkManager().updateModalPathInternalNavigation(path, modalSettings, addHistoryEntry);
  }
  static navigateToIntent(semanticSlug, params) {
    LuigiClient.linkManager().navigateToIntent(semanticSlug, params);
  }
  static getCurrentRoute(config = {
    fromVirtualTreeRoot: true
  }) {
    return new Promise((resolve, reject) => {
      let lm = LuigiClient.linkManager();
      if (config.fromVirtualTreeRoot) {
        lm = lm.fromVirtualTreeRoot();
      } else if (config.fromContext === true) {
        lm = lm.fromClosestContext();
      } else if (config.fromContext) {
        lm = lm.fromContext(config.fromContext);
      }
      lm.getCurrentRoute().then(resolve, reject);
    });
  }
  static getNodeParams(shouldDesanitise = true) {
    const params = LuigiClient.getNodeParams(shouldDesanitise);
    return params;
  }
  static getPathParams() {
    const params = LuigiClient.getPathParams();
    return params;
  }
  static getSearchParams() {
    const params = LuigiClient.getCoreSearchParams();
    return params;
  }
  static addNodeParams(params, keepBrowserHistory) {
    LuigiClient.addNodeParams(params, keepBrowserHistory);
  }
  static addSearchParams(params, keepBrowserHistory) {
    LuigiClient.addCoreSearchParams(params, keepBrowserHistory);
  }
  static openAsModal(path, modalSettings, nodeParams) {
    return new Promise((resolve, reject) => {
      if (path) {
        const id = helpers.subscribe(null, {
          name: 'openAsModalCallback',
          resolve,
          reject
        });
        LuigiClient.linkManager().fromVirtualTreeRoot().withParams({
          ...nodeParams,
          remotePromiseId: String(id)
        })
        // .withOptions({ preventHistoryEntry: true })
        .openAsModal(path, modalSettings);
      }
    });
  }
  static closeModal(value) {
    LuigiClient.linkManager().goBack(value);
  }
  static goBack(value) {
    LuigiClient.linkManager().goBack(value);
  }
  static broadcastMessageToAllIframes(message, options) {
    const {
      frames
    } = window.parent;
    for (let i = 0; i < frames.length; i++) {
      frames[i].postMessage(message, options);
    }
  }
}

export { LuigiHelper as default };
